import {
    createStore
} from 'vuex'
export default createStore({
    state: {
        addListText: [],
        endList: []
    },
    mutations: {
        add(state, action) {
            state.addListText.push(action.text);
        },
        // clear(state) {
        //     state.addListText = [];
        //     // sessionStorage.clear();
        //     // state.addListText = state.temp;
        // },
        delete(state, action) {
            const id = state.addListText.indexOf(action.text);
            state.addListText.splice(id, 1);
            sessionStorage.setItem(
                "store",
                JSON.stringify(state))
        },
        deleteEnd(state, action) {
            const id = state.endList.indexOf(action.text);
            state.endList.splice(id, 1);
            sessionStorage.setItem(
                "store",
                JSON.stringify(state))
        },
    },
    actions: {

    },
    getters: {

    }
})