<template>
  <div class="box">
    <info :path="path" :title="title" :text="info"></info>
    <add-list @send="getInfo"></add-list>
  </div>
</template>
<script>
import { ref } from "vue";
import info from "../components/info.vue";
import addList from "../components/addList.vue";
export default {
  name: "课后作业页",
  components: { info, addList },
  setup() {
    const path = ref("/");
    const title = ref("课后作业");
    const info = ref("");
    const getInfo = (v) => {
      info.value = v;
    };
    return { path, title, info, getInfo };
  },
};
</script>
