<template>
  <div class="box">
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
  </div>
</template>
<script>
import { NavBar } from "vant";
export default {
  name: "上导航",
  components: { [NavBar.name]: NavBar },
  props: { title: String, path: String },
  methods: {
    onClickLeft() {
      this.$router.push(this.path);
    },
  },
  setup() {
    return {};
  },
};
</script>