<template>
  <div class="box">
    <van-cell center :title="title">
      <template #right-icon>
        <van-switch v-model="checked" size="24" />
      </template>
    </van-cell>
  </div>
</template>
<script>
import { ref } from "vue";
import { Switch, Cell, CellGroup } from "vant";
export default {
  name: "switch-开关",
  components: {
    [Switch.name]: Switch,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
  },
  props: { title: String },
  setup() {
    const checked = ref("");
    const onUpdateValue = (newValue) => {
      checked.value = newValue;
    };
    return { checked, onUpdateValue };
  },
};
</script>
