import {
  createRouter,
  createWebHashHistory
} from 'vue-router'

import main from "../views/main.vue"
import user2 from "../components/user2.vue"
import userSet from "../components/userSet.vue"
import userSet2 from "../components/userSet2.vue"
import myDay from "../components/myDay.vue"
import renWu from "../components/renWu.vue"
import homeWork from "../components/homeWork.vue"
import end from "../components/end.vue"
const routes = [{
  path: "/",
  component: main
}, {
  path: "/user2",
  component: user2
}, {
  path: "/userSet",
  component: userSet
}, {
  path: "/userSet2",
  component: userSet2
}, {
  path: "/myDay/:title",
  component: myDay,
  name: "myDay"
}, {
  path: "/renWu",
  component: renWu
}, {
  path: "/homeWork",
  component: homeWork
}, {
  path: "/end",
  component: end,
  name: "end"
}]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router