<template>
  <div class="box">
    <span>
      <van-image round :width="width" :height="height" :src="src" />
    </span>
    <div class="d1">
      <span> {{ list.name }} </span>
      <span> {{ list.email }} </span>
    </div>
  </div>
</template>
<script>
import { Image as VanImage } from "vant";
import { ref } from "vue";
export default {
  name: "userPage",
  components: { [VanImage.name]: VanImage },
  setup() {
    const width = ref("5rem");
    const height = ref("5rem");
    const src = ref("https://z3.ax1x.com/2021/05/22/gLRhkT.jpg");
    var list = ref("");
    // var { ctx } = getCurrentInstance(); //ctx固定 解构赋值
    const axios = require("axios").default;
    axios
      .get(
        "https://raw.githubusercontent.com/lonely-zhou/todoapp/main/json/data.json"
      )
      .then(function (response) {
        // 处理成功情况
        list.value = response.data.user;
        // console.log(response);
      })
      .catch(function (error) {
        // 处理错误情况
        console.log(error);
      })
      .then(function () {
        // 总是会执行
      });
    // onMounted(() => {
    //   ctx.$axios
    //     .get("http://rap2api.taobao.org/app/mock/284147/user")
    //     .then((res) => {
    //       // console.log(res.data);
    //       list.value = res.data;
    //     });
    // });
    return {
      width,
      height,
      src,
      list,
    };
  },
};
</script>
<style scoped>
.box,
.d1 {
  display: flex;
  flex-grow: 1;
}
.d1 {
  display: flex;
  flex-flow: column;
  flex-grow: 2;
}
span {
  margin: 1rem 0 0.5rem 1rem;
}
</style>