<template>
  <div class="box">
    <nav-bar :title="title" :path="path"></nav-bar>
    <user></user>
    <van-divider :style="{ color: '#1989fa', borderColor: '#1989fa' }" />
    <div class="d2">
      <van-cell is-link @click="showPopup">
        <span>添加账户</span>
      </van-cell>
      <van-popup v-model:show="show" position="top" :style="{ height: '100%' }">
        <div class="login">
          <button @click="showPopup">关闭</button>
          <p id="login"></p>
          <h1>Login</h1>
          <van-form @submit="onSubmit">
            <van-field
              v-model="state.username"
              name="用户名"
              label="用户名"
              placeholder="用户名"
              :rules="[{ required: true, message: '请填写用户名' }]"
            />
            <van-field
              v-model="state.password"
              type="password"
              name="密码"
              label="密码"
              placeholder="密码"
              :rules="[{ required: true, message: '请填写密码' }]"
            />
            <div style="margin: 16px">
              <van-button round block type="primary" native-type="submit">
                提交
              </van-button>
            </div>
          </van-form>
          <!-- <form method="post">
            <input
              type="text"
              required="required"
              placeholder="用户名"
              name="username"
            />
            <input
              type="password"
              required="required"
              placeholder="密码"
              name="password"
            />
            <button class="but" type="submit">登录</button>
          </form> -->
        </div>
      </van-popup>
    </div>
  </div>
</template>
<script>
import { ref, reactive } from "vue";
import { Divider, Popup, Form, Field, Button } from "vant";
import user from "./user.vue";
import navBar from "./navBar.vue";
export default {
  name: "管理账户页面",
  components: {
    user,
    navBar,
    [Divider.name]: Divider,
    [Popup.name]: Popup,
    [Form.name]: Form,
    [Field.name]: Field,
    [Button.name]: Button,
  },
  setup() {
    const title = ref("管理账户");
    const path = ref("/user2");
    const show = ref(false);
    const showPopup = () => {
      show.value = !show.value;
    };
    const state = reactive({
      username: "",
      password: "",
    });
    const onSubmit = (values) => {
      console.log("submit", values);
    };
    return {
      title,
      path,
      showPopup,
      show,
      state,
      onSubmit,
    };
  },
};
</script>