<template>
  <div class="box">
    <nav-bar :path="path" :title="title"></nav-bar>
    <div>
      <ul id="u1">
        <li v-for="(v, i) in list1" :key="i" :id="i">
          <span @click="goto(v)">{{ v }}</span>
          <button @click="del(i)">删除</button>
        </li>
      </ul>
      <ul id="u2">
        <li v-for="(v, i) in addListText" :key="i" :id="'text' + i">
          <span @click="goto(v)">{{ v }}</span>
          <button @click="deleteInfo(i, v)">删除</button>
        </li>
      </ul>
      <van-cell center title="已完成" @click="show">
        <template #right-icon>
          <van-switch v-model="showGetDo" size="24" @click="show" />
        </template>
      </van-cell>
      <ul class="getdo" v-show="showGetDo">
        <li v-for="(v, i) in list2" :key="i" :id="i">
          <span @click="goto(v)">{{ v }}</span>
          <button @click="del(i, v)">删除</button>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import router from "../router/index.js";
import navBar from "../components/navBar.vue";
import { Switch, Cell, CellGroup } from "vant";
import { ref, computed } from "vue";
import { useStore } from "vuex";
export default {
  name: "内容页",
  created() {
    //   //在页面加载时读取sessionStorage里的状态信息
    if (sessionStorage.getItem("store")) {
      this.$store.replaceState(
        Object.assign(
          {},
          this.$store.state,
          JSON.parse(sessionStorage.getItem("store"))
        )
      );
    }
  },
  //   //在页面刷新时将vuex里的信息保存到sessionStorage里
  //   window.addEventListener("beforeunload", () => {
  //     sessionStorage.setItem(
  //       "store",
  //       JSON.stringify(this.$store.state)
  //     );
  //   });
  // },
  components: {
    navBar,
    [Switch.name]: Switch,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
  },
  props: { path: String, title: String, text: String },
  watch: {
    text: function () {
      if (this.text != null) {
        this.addListText.push(this.text);
        // this.temp.push(this.text);
        sessionStorage.setItem("store", JSON.stringify(this.$store.state));
      }
    },
  },
  setup() {
    const axios = require("axios").default;
    const list1 = ref("");
    const list2 = ref("");
    const showGetDo = ref(false);
    const store = useStore();
    var addListText = computed(() => store.state.addListText);
    var temp = computed(() => store.state.temp);
    // store.commit("add", { text: addListText });
    // store.commit("clear");
    axios
      .get(
        "https://raw.githubusercontent.com/lonely-zhou/todoapp/main/json/data.json"
      )
      .then(function (response) {
        list1.value = response.data.homework;
        list2.value = response.data.getdo;
      })
      .catch(function (error) {
        console.log(error);
      });
    const show = () => {
      if (showGetDo.value == false) {
        showGetDo.value = true;
      } else {
        showGetDo.value = false;
      }
    };
    const del = (i) => {
      const li = document.getElementById(i);
      li.style.display = "none";
    };
    const goto = (v) => {
      router.push({
        path: "end",
        query: { title: v },
      });
    };
    const deleteInfo = (i, v) => {
      // const li = document.getElementById("text" + i);
      // li.style.display = "none";
      store.commit("delete", { text: v });
    };
    return {
      list1,
      list2,
      show,
      showGetDo,
      del,
      goto,
      addListText,
      deleteInfo,
      temp,
    };
  },
};
</script>
<style scoped>
li {
  border: 1px solid black;
  margin: 1rem;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
}
.getdo span {
  text-decoration: line-through;
}
</style>
