<template>
  <div class="box">
    <nav-bar title="end" path="/homeWork"></nav-bar>
    <h2>{{ title }}</h2>
    <ul>
      <li v-for="(v, i) in endList" :key="i" :id="end + i">
        <span @click="goto(v)">第{{ i + 1 }}步 --- {{ v }}</span>
        <button @click="deleteEndInfo(i, v)">删除</button>
      </li>
    </ul>
  </div>
  <add-list @send="getInfo"></add-list>
</template>
<script>
import { useRoute } from "vue-router";
import AddList from "./addList.vue";
import { ref, computed } from "vue";
import { useStore } from "vuex";
import navBar from "./navBar.vue";
export default {
  name: "详情页",
  components: { AddList, navBar },
  created() {
    if (sessionStorage.getItem("store")) {
      this.$store.replaceState(
        Object.assign(
          {},
          this.$store.state,
          JSON.parse(sessionStorage.getItem("store"))
        )
      );
    }
  },
  watch: {
    endInfo: function () {
      if (this.endInfo != null) {
        this.endList.push(this.endInfo);
        sessionStorage.setItem("store", JSON.stringify(this.$store.state));
      }
    },
  },
  setup() {
    const title = ref("");
    const route = useRoute();
    title.value = route.query.title;
    const store = useStore();
    const endInfo = ref("");
    const endList = computed(() => store.state.endList);
    const getInfo = (v) => {
      endInfo.value = v;
    };
    const deleteEndInfo = (i, v) => {
      // const li = document.getElementById("text" + i);
      // li.style.display = "none";
      store.commit("deleteEnd", { text: v });
    };
    return { title, getInfo, endInfo, endList, deleteEndInfo };
  },
};
</script>
<style scoped>
li {
  border: 1px solid black;
  margin: 1rem;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
}
</style>