<template>
  <div class="box1">
    <div>
      <van-cell is-link @click="showPopup">
        <span class="iconfont icon-xinjian2"> </span>
      </van-cell>
      <van-popup
        v-model:show="show"
        position="bottom"
        :style="{ height: '20%' }"
      >
        <input v-model="info" type="text" />
        <van-button type="primary" block size="large" @click="showPopup">
          确定
        </van-button>
      </van-popup>
    </div>
  </div>
</template>
<script>
import { Popup, Field, Tabbar, TabbarItem, Popover, Button } from "vant";
export default {
  name: "new",
  components: {
    [Popup.name]: Popup,
    [Field.name]: Field,
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem,
    [Popover.name]: Popover,
    [Button.name]: Button,
  },
  data() {
    return {
      show: false,
      info: "",
    };
  },
  methods: {
    showPopup() {
      if (this.show == true) {
        this.$emit("send", this.info);
        this.info = "";
        this.show = false;
      } else {
        this.show = true;
      }
    },
  },
};
</script>
<style scoped>
.icon-xinjian2 {
  font-size: 4rem;
  color: #1989fa;
}
.box1 > div span {
  position: fixed;
  bottom: 0.5rem;
  right: 1rem;
}
input {
  width: 100%;
  height: 50%;
}
</style>