<template>
  <div class="box">
    <nav-bar title="设置" path="/user2"></nav-bar>
    <div class="d1">
      <user></user>
      <divider :style="style1" />
      <p>管理账户</p>
      <divider :style="style1" />
      <p>注销</p>
    </div>
    <div class="d2">
      <divider :style="style2" />
      <p>常规</p>
      <div v-for="(v, i) in list1" :key="i">
        <switchPage :title="v"></switchPage>
      </div>
      <divider :style="style2" />
      <div v-for="(v, i) in list2" :key="i">
        <switchPage :title="v"></switchPage>
      </div>
      <divider :style="style2" />
      <div v-for="(v, i) in list3" :key="i">
        <switchPage :title="v"></switchPage>
      </div>
      <div class="d3">
        <divider :style="style2" />
        <p>帮助与反馈</p>
        <ul v-for="(v, i) in list4" :key="i">
          <li>{{ v }}</li>
        </ul>
      </div>
      <div class="d4">
        <divider :style="style2" />
        <p>连接</p>
        <ul v-for="(v, i) in list5" :key="i">
          <li>{{ v }}</li>
        </ul>
      </div>
      <div class="d5">
        <divider :style="style2" />
        <p>关于</p>
        <ul v-for="(v, i) in list6" :key="i">
          <li>{{ v }}</li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import { ref } from "vue";
import navBar from "./navBar.vue";
import user from "./user.vue";
import divider from "./divider.vue";
import switchPage from "./switchPage.vue";
export default {
  name: "用户设置页面",
  components: {
    navBar,
    user,
    divider,
    switchPage,
  },
  setup() {
    const style1 = ref({
      color: "#1989fa",
      borderColor: "#1989fa",
      padding: "0 1rem 0 6rem",
    });
    const style2 = ref({
      color: "#1989fa",
      borderColor: "#1989fa",
    });
    var list1 = ref("");
    var list2 = ref("");
    var list3 = ref("");
    var list4 = ref("");
    var list5 = ref("");
    var list6 = ref("");
    const axios = require("axios").default;
    axios
      .get(
        "https://raw.githubusercontent.com/lonely-zhou/todoapp/main/json/data.json"
      )
      .then(function (response) {
        list1.value = response.data.changgui;
        list2.value = response.data.zhinenglist;
        list3.value = response.data.tongzhi;
        list4.value = response.data.help;
        list5.value = response.data.link;
        list6.value = response.data.guanyu;
      })
      .catch(function (error) {
        console.log(error);
      });
    return { style1, style2, list1, list2, list3, list4, list5, list6 };
  },
};
</script>
<style scoped>
.d1 p,
.d2 p,
.d3 p,
.d4 p,
.d5 p {
  color: rgb(5, 110, 248);
}
.d1 p {
  margin-left: 6rem;
}
li {
  margin: 1rem 0 0 1rem;
}
</style>