<template>
  <div class="box">
    <van-cell is-link @click="showPopup">
      <div class="iconfont icon-fangdajing"></div>
    </van-cell>
      <van-popup v-model:show="show" position="top" :style="{ height: '100%' }">
        <div class="d1">
          <span @click="showPopup">返回</span>
          <input type="search" placeholder="搜索"/>
          <span>更多</span>
        </div>
      </van-popup>
  </div>
</template>
<script>
import { Popup } from "vant";
import { ref } from "vue";
export default {
  name: "searchPage",
  components: { [Popup.name]: Popup },
  setup() {
    const show = ref(false);
    const showPopup = () => {
      show.value = !show.value;
    };
    return {
      show,
      showPopup,
    };
  },
};
</script>
<style scoped>
.box {
  z-index: 9;
  position: fixed;
  top: 1rem;
  right: 1rem;
}
.icon-fangdajing {
  font-size: 2rem;
}
input {
  width: 80%;
}
.d1 {
  display: flex;
}
</style>